import React, {useState, useRef, useEffect} from "react";
import taxi from "./taxi_edit.png"

function Taxi() {
  const [inputs, setInputs] = useState({
    name: ""
  })
  // const image = React.useRef(null);
  const canvas = useRef()
  const { name } = inputs

  function InputText(inputs) {
    const ctx = canvas.current.getContext("2d")
    ctx.textBaseline = 'middle'
    ctx.font = "bold 38px NanumGothic extra-condensed"
    ctx.fillStyle = '#338c9a'
    ctx.textBaseline = 'middle'
    ctx.fillText(inputs, 130, 670)
  }

  function draw(ctx){
    ctx.clearRect(0,0,canvas.width, canvas.height);
    const _image = new Image()
    _image.src = taxi
    _image.onload = () => {
      ctx.drawImage(_image, 0, 0)
    }
  }

  function downloadCanvas() {

    const url = canvas.current.toDataURL('image/png')
    const link = document.createElement('a')
    link.href = url;
    link.setAttribute('download','my_way.png')
    document.body.appendChild(link)
    link.click()
    document.body.removeChild(link)
  }



  useEffect(() =>{
    const ctx = canvas.current.getContext("2d")
    draw(ctx)
  }, [])

  //
  // useEffect(() => {
  //   InputText(name)
  // }, [name])

  const onChange = (e) => {

    const { value, name: blue_text } = e.target
    setInputs({
      ...inputs,
      [blue_text]: value
    })
  }

  const onReset = () => {
    const ctx = canvas.current.getContext("2d")
    setInputs({
      name: ''
    })
    ctx.clearRect(0,0,canvas.width, canvas.height);
    draw(ctx)

  }

  const adjust = () => {
    InputText(name)
  }

  return (
      <div>
        <div className="container">
          <canvas
              ref={canvas}
              width={1440}
              height={792}
              onLoad={() =>{
                const cnv = canvas.current
                const ctx = cnv.getContext("2d")
                draw(ctx)
              }}
          />
          <br />

          <input className="form-control form-control-lg" name="name" placeholder="ex) 개발자님 야근 안하고 어디가세요?" rows={2} cols={20} maxLength={40} onChange={onChange} value={name}/>
          <br />
          <div className='row'>
            <button className="btn btn-primary" type="button" onClick={adjust}>적용</button>&nbsp;
            <button className="btn btn-info" type="button" onClick={onReset}>초기화</button>&nbsp;
            <button className="btn btn-success" type="button" onClick={downloadCanvas}>다운로드</button>
          </div>
        </div>

      </div>
  )
}

export default Taxi